<template>
    <div>
        <div class="row">
            <p class="tit">随机筛选</p>
        </div>
        <div class="row loading" v-show="!dishList">
            <i class="el-icon-loading"></i>
        </div>
        <div class="row" v-show="dishList">
            <div v-for="(item, index) in dishList" :key="index" class="dish" v-show="index==startPos">
                <el-card :body-style="{ padding: '0px' }">
                    <img :src="item.pic" class="image">
                    <div style="padding: 14px;">
                        <span>{{item.name}}</span>
                        <div class="bottom clearfix">
                            <el-rate :value="item.rate|getAvg" disabled show-text text-color="#ff9900" text-template="{value}">
                            </el-rate>
                            <div class="tag">
                                <span class="tag">{{item.taste | tasteFilter}}</span>
                                <span class="tag">{{item.meat | meatFilter}}</span>
                            </div>
                        </div>
                    </div>
                </el-card>
            </div>
        </div>
        <div class="row row3">
            <el-button type="primary" @click="stopRandom" v-show="isRandom" class="random-btn">停止</el-button>
            <el-button type="primary" @click="startRandom" v-show="!isRandom" class="random-btn">开始</el-button>
            <!-- <el-button type="primary">条件筛选</el-button> -->
        </div>
    </div>
</template>
<script>
    export default {
        name: 'hello',
        data() {
            return {
                dishList: null,
                randomTimeout: 0,
                randomSpeed: 100,//毫秒
                randomCount: 15,
                isRandom: false,
                startPos: 0
            }
        },
        methods: {
            stopRandom() {
                clearTimeout(this.randomTimeout)
                this.isRandom = false
            },
            startRandom() {
                this.isRandom = true
                this.startPos++
                if (this.startPos == this.randomCount) {
                    this.startPos = 0
                }
                this.randomTimeout = setTimeout(() => {
                    this.startRandom()
                }, this.randomSpeed)

            }
        },
        mounted() {
            this.axios.get(apiUrl + '/eatApi/dish/random', { params: { count: this.randomCount } }).then(res => {
                var data = res.data
                if (data.msgcode == 1) {
                    this.dishList = data.dishList
                    this.startRandom()
                } else {
                    this.stopRandom()
                    this.$message({
                        message: data.msg,
                        type: 'warning'
                    })
                }
            }).catch(err => {
                console.log(err)
                this.stopRandom()
                this.$message({
                    message: '信息获取失败，尝试刷新页面',
                    type: 'warning'
                })
            })
        },
        filters: {
            dateFilter(date) {
                return date.split('T')[0]
            },
            getAvg(array) {
                if (array.length) {
                    return parseFloat((array.reduce((pre, cur) => cur + pre) / array.length).toFixed(1))
                } else {
                    return 5
                }
            },
            tasteFilter(val) {
                switch (val) {
                    case '1':
                        return '酸'
                        break;
                    case '2':
                        return '甜'
                        break;
                    case '3':
                        return '辣'
                        break;
                    case '4':
                        return '鲜'
                        break;
                }
            },
            meatFilter(val) {
                switch (val) {
                    case '0':
                        return '荤素搭配'
                        break;
                    case '1':
                        return '主荤'
                        break;
                    case '2':
                        return '主素'
                        break;
                }
            }
        }
    }

</script>

<style scoped>
    .tit {
        color: #20A0FF;
    }

    .time {
        font-size: 13px;
        color: #999;
    }

    .bottom {
        margin-top: 13px;
        line-height: 12px;
    }

    .button {
        padding: 0;
        float: right;
    }

    .image {
        width: 100%;
        display: block;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .row3 {
        position: absolute;
        bottom: -50px;
    }

    .random-btn {
        margin-left: 0;
    }

    .loading {
        text-align: center;
    }

    .loading i {
        font-size: 28px;
    }
</style>


